
import {
  Button,
  Card,
  Checkbox,
  Form,
  FormItem,
  Input,
  InputPassword,
} from 'ant-design-vue';
import { ValidateErrorEntity } from 'ant-design-vue/es/form/interface';
import { Content } from 'ant-design-vue/es/layout/layout';
import { defineComponent, reactive } from 'vue';

const AntdComponents = {
  Card,
  Form,
  FormItem,
  Input,
  InputPassword,
  Checkbox,
  Button,
};
export default defineComponent({
  name: 'LoginPage',

  components: {
    ...AntdComponents,
    Content,
  },

  setup() {
    const formState = reactive({
      username: '',
      password: '',
    });
    return { formState };
  },

  methods: {
    onFinish(values: { username: string; password: string }) {
      console.log('Success:', values);
    },

    onFinishFailed(errorInfo: ValidateErrorEntity<any>) {
      console.log('Failed:', errorInfo);
    },
    loginAsTest() {
      this.formState.username = 'joe.deon';
      this.formState.password = 'password';
      this.onFinish(this.formState);
    },
  },
});
